<template>
    <div>
<!--        Header-->
        <div id="top">
            <div id="logo">
                <img class="logo" :src="wData.logo" alt="Logo">
                <h1 :style="{color: wData['LColor']}">{{ wData.title }}</h1>
            </div>
        </div>

<!--        Login-->
        <nav-login />

<!--        Footer-->
        <div id="footer">
            
            <div class="inline" v-for="d in wData['text']" :key="d">
                <div v-if="d['TextLink']">
                    &ensp;&ensp;|&ensp;&ensp;
                    <a :href="d['TextLink']" :style="{color: wData['FColor']}">
                        {{ d["Text"] }}
                    </a>
                </div>
                <div v-else>
                    &ensp;&ensp;|&ensp;&ensp;
                    <a :style="{color: wData['FColor']}">
                        {{ d["Text"] }}
                    </a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import navLogin from "@/components/navLogin";

export default {
    name: "LoginView",
    components: {
        navLogin,
    },
    computed: {
        // 
        wData(){
            return this.$store.state.LoginConfig
        }
    }
}
</script>

